import { CoursesService } from './../courses.service';
import { MessageService } from 'src/app/service/message.service';
import { DetailsCourseComponent } from './../dialogs/details-course/details-course.component';
import {
  AfterViewInit,
  Component,
  OnDestroy,
  OnInit,
  ViewChild,
} from '@angular/core';
import { FormControl } from '@angular/forms';
import { ReplaySubject, Subject } from 'rxjs';
import { take, takeUntil } from 'rxjs/operators';
import { MatSelect } from '@angular/material/select';
import { MatDialog } from '@angular/material/dialog';
import { ShoppingService } from 'src/app/pages/shopping/shopping.service';
import { environment } from '../../../../../environments/environment';

export interface COURSE {
  id: any;
  video?: string;
  poster?: string;
  name: string;
  category: string;
  uploader: string;
  description?: string;
  length: string;
  price: number;
  status: number;
}

@Component({
  selector: 'app-courses',
  templateUrl: './courses.component.html',
  styleUrls: ['./courses.component.scss'],
})
export class CoursesComponent implements OnInit, OnDestroy {
  displayedColumns: string[] = [
    'video',
    'name',
    'category',
    'uploader',
    'length',
    'price',
    'add',
  ];
  listCourses: COURSE[];
  category: string;
  listCategory: string[];
  query: string = '';
  constructor(
    public dialog: MatDialog,
    private message: MessageService,
    public services: CoursesService,
    public shoppingService: ShoppingService
  ) {}
  ngOnInit() {
    this.categories();
    this.courses();
    this.shoppingService.listBasket().subscribe((res) => {
      let data = res?.basket;
      this.message.basketList = res?.basket ?? [];
      this.message.basket.next(res?.basket);
    });
  }
  categories() {
    this.services.categories().subscribe((res) => {
      this.listCategory = res.categories;
    });
  }
  courses(query?: string, category?: string) {
    this.services
      .getCourses(
        undefined,
        query ? query : undefined,
        category ? category : undefined
      )
      .subscribe((res) => {
        console.log(res);
        let data = res.courses;
        this.listCourses = [];
        for (const course of data) {
          this.listCourses.push({
            id: course?._id,
            name: course?.name,
            category: course?.category,
            description: course?.description,
            price: course?.price,
            length: new Date(course?.length * 1000).toISOString().substr(14, 5),
            status: this.convertStatus(course?.status)!,
            poster: course?.thumbnail,
            uploader: course?.owner?.firstName + ' ' + course?.owner?.lastName,
            video: course?.file ? `${window.origin}/${course?.file}` : '',
          });
        }
      });
  }
  changeCatrgory(e) {
    this.courses(this.query, e.value);
  }
  ngOnDestroy() {}

  openDialog() {
    const dialogRef = this.dialog.open(DetailsCourseComponent, {
      width: '900px',
      data: {},
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
  addToBasket(data) {
    this.services.changeBasket('add', data?.id).subscribe((res) => {
      this.message.basketList.push({
        id: data?.id,
        thumbnail: data?.poster,
        name: data.name,
        price: data.price,
      });
      this.message.basket.next(this.message.basketList);
    });
  }
  convertStatus(status) {
    let st: number;
    switch (status) {
      case 'pending':
        st = 1;
        break;
      case 'approved':
        st = 2;
        break;
      case 'rejected':
        st = 3;
        break;

      default:
        break;
        return st;
    }
  }
}
